import React, { Component } from 'react';

import { getClaimsData } from '../../actions/claimsActions';
import Claims from '../../components/Claims/Claims';

class ClaimsContainer extends Component {
  constructor(props) {
    super(props);

    this.state = {
      data: getClaimsData(),
    };
  }

  render() {
    const { data } = this.state;

    return (
      <Claims data={data} />
    );
  }
}

ClaimsContainer.defaultProps = {
};

ClaimsContainer.propTypes = {
};

export default ClaimsContainer;
