/**
 * Claims.js
 * Claims page
 *
 * @version 0.0.1
 * @author [Barry](Barry@policypal.com)
 */


import React, { Component } from 'react';
import { Row, Col } from 'reactstrap';
import PropTypes from 'prop-types';

import _ from 'lodash';

import DropDownV2 from '../Forms/DropDown/DropDownV2';

class Category extends Component {
  constructor(props) {
    super(props);
    this.state = {
      width: window.innerWidth,
      isDeathActive: 'Death',
      isCriticalIllnessActive: false,
      isDisabilityActive: false,
      isMedicalActive: false,
      isPersonalAccidentActive: false,
      isTravelActive: false,
    };
    this.onClick = this.onClick.bind(this);
    this.handleWindowSizeChange = this.handleWindowSizeChange.bind(this);
  }

  componentWillMount() {
    window.addEventListener('resize', this.handleWindowSizeChange);
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.handleWindowSizeChange);
  }

  onClick(desc) {
    switch (desc) {
      case 'Death': return (
        this.setState({
          isDeathActive: 'Death',
          isCriticalIllnessActive: false,
          isDisabilityActive: false,
          isMedicalActive: false,
          isPersonalAccidentActive: false,
          isTravelActive: false,
        })
      );
      case 'Critical Illness': return (
        this.setState({
          isDeathActive: false,
          isCriticalIllnessActive: true,
          isDisabilityActive: false,
          isMedicalActive: false,
          isPersonalAccidentActive: false,
          isTravelActive: false,
        })
      );
      case 'Disability': return (
        this.setState({
          isDeathActive: false,
          isCriticalIllnessActive: false,
          isDisabilityActive: true,
          isMedicalActive: false,
          isPersonalAccidentActive: false,
          isTravelActive: false,
        })
      );
      case 'Medical': return (
        this.setState({
          isDeathActive: false,
          isCriticalIllnessActive: false,
          isDisabilityActive: false,
          isMedicalActive: true,
          isPersonalAccidentActive: false,
          isTravelActive: false,
        })
      );
      case 'Personal Accident': return (
        this.setState({
          isDeathActive: false,
          isCriticalIllnessActive: false,
          isDisabilityActive: false,
          isMedicalActive: false,
          isPersonalAccidentActive: true,
          isTravelActive: false,
        })
      );
      case 'Travel': return (
        this.setState({
          isDeathActive: false,
          isCriticalIllnessActive: false,
          isDisabilityActive: false,
          isMedicalActive: false,
          isPersonalAccidentActive: false,
          isTravelActive: true,
        })
      );
      default: return (
        this.setState({
          isDeathActive: true,
          isCriticalIllnessActive: false,
          isDisabilityActive: false,
          isMedicalActive: false,
          isPersonalAccidentActive: false,
          isTravelActive: false,
        })
      );
    }
  }

  handleWindowSizeChange() {
    this.setState({ width: window.innerWidth });
  }

  iterateDocumentList(categoryIndex) {
    const { data } = this.props;
    return (
      <Col className="document-list">
        {
          _.map(data.documentsList[categoryIndex].documents, (d, i) => (
            <Row key={`doc-${i}`} className="document-item">
              <Col xs="1">
                <div className="item-number"><span className="number">{i + 1}</span></div>
              </Col>
              <Col xs="1">
                <img className="item-icon" alt="icon" src={d.iconSrc} />
              </Col>
              <Col xs="10">
                <div className="item-desc">{d.desc}</div>
              </Col>
            </Row>
          ))
        }
      </Col>
    );
  }

  iterateCompanyList(companyIndex) {
    const { data } = this.props;
    return (
      <Row className="company-list">
        {
          _.map(data.companyList[companyIndex], (d, index) => (
            <Col key={`company-${index}`} className="company">
              <div className="img-container">
                <a href={d.url}>
                  <img src={d.iconSrc} alt="" />
                </a>
              </div>
            </Col>
          ))
        }
      </Row>
    );
  }

  renderCategory(c) {
    switch (c.desc) {
      case 'Death':
        return (
          <div
            onClick={() => this.onClick(c.desc)}
            onKeyPress={() => {}}
            className={this.state.isDeathActive ? 'category-selected-block' : 'category-block'}
            role="button"
            tabIndex="0"
            key={c.desc}
          >
            <img src={c.iconSrc} alt={c.desc} />
            <p className="category-desc">{c.desc}</p>
          </div>
        );
      case 'Critical Illness':
        return (
          <div
            onClick={() => this.onClick(c.desc)}
            onKeyPress={() => {}}
            className={this.state.isCriticalIllnessActive ? 'category-selected-block' : 'category-block'}
            role="button"
            tabIndex="0"
            key={c.desc}
          >
            <img src={c.iconSrc} alt={c.desc} />
            <p className="category-desc">{c.desc}</p>
          </div>
        );
      case 'Disability':
        return (
          <div
            onClick={() => this.onClick(c.desc)}
            onKeyPress={() => {}}
            className={this.state.isDisabilityActive ? 'category-selected-block' : 'category-block'}
            role="button"
            tabIndex="0"
            key={c.desc}
          >
            <img src={c.iconSrc} alt={c.desc} />
            <p className="category-desc">{c.desc}</p>
          </div>
        );
      case 'Medical':
        return (
          <div
            onClick={() => this.onClick(c.desc)}
            onKeyPress={() => {}}
            className={this.state.isMedicalActive ? 'category-selected-block' : 'category-block'}
            role="button"
            tabIndex="0"
            key={c.desc}
          >
            <img src={c.iconSrc} alt={c.desc} />
            <p className="category-desc">{c.desc}</p>
          </div>
        );
      case 'Personal Accident':
        return (
          <div
            onClick={() => this.onClick(c.desc)}
            onKeyPress={() => {}}
            className={this.state.isPersonalAccidentActive ? 'category-selected-block' : 'category-block'}
            role="button"
            tabIndex="0"
            key={c.desc}
          >
            <img src={c.iconSrc} alt={c.desc} />
            <p className="category-desc">{c.desc}</p>
          </div>
        );
      case 'Travel':
        return (
          <div
            onClick={() => this.onClick(c.desc)}
            onKeyPress={() => {}}
            className={this.state.isTravelActive ? 'category-selected-block' : 'category-block'}
            role="button"
            tabIndex="0"
            key={c.desc}
          >
            <img src={c.iconSrc} alt={c.desc} />
            <p className="category-desc">{c.desc}</p>
          </div>
        );
      default: return (
        <div
          onClick={() => this.onClick(c.desc)}
          onKeyPress={() => {}}
          className={this.state.isDeathActive ? 'category-selected-block' : 'category-block'}
          role="button"
          tabIndex="0"
          key={c.desc}
        >
          <img src={c.iconSrc} alt={c.desc} />
          <p className="category-desc">{c.desc}</p>
        </div>
      );
    }
  }

  renderDocumentList(title) {
    switch (title) {
      case 'Death': return (this.iterateDocumentList(0));
      case 'Critical Illness': return (this.iterateDocumentList(1));
      case 'Disability': return (this.iterateDocumentList(2));
      case 'Medical': return (this.iterateDocumentList(3));
      case 'Personal Accident': return (this.iterateDocumentList(4));
      case 'Travel': return (this.iterateDocumentList(5));
      default: return (this.iterateDocumentList(0));
    }
  }

  renderCompanyList(title) {
    switch (title) {
      case 'Death': return (this.iterateCompanyList(0));
      case 'Critical Illness': return (this.iterateCompanyList(1));
      case 'Disability': return (this.iterateCompanyList(2));
      case 'Medical': return (this.iterateCompanyList(3));
      case 'Personal Accident': return (this.iterateCompanyList(4));
      case 'Travel': return (this.iterateCompanyList(5));
      default: return (this.iterateCompanyList(6));
    }
  }

  renderDocument(colSize) {
    let title = 'Death';
    if (this.state.isDeathActive) {
      title = 'Death';
    } else if (this.state.isCriticalIllnessActive) {
      title = 'Critical Illness';
    } else if (this.state.isDisabilityActive) {
      title = 'Disability';
    } else if (this.state.isMedicalActive) {
      title = 'Medical';
    } else if (this.state.isPersonalAccidentActive) {
      title = 'Personal Accident';
    } else {
      title = 'Travel';
    }

    return (
      <Col xs={colSize} className="document-section">
        <p className="title">{title} Claims</p>
        <p className="sub-title">Documents you might need for the claim</p>
        <div className="line" />
        {this.renderDocumentList(title)}
        <p className="title">Company</p>
        <p className="sub-title">Select the company you are claiming from</p>
        <div className="line" />
        {this.renderCompanyList(title)}
      </Col>
    );
  }

  renderGuide() {
    const { data } = this.props;
    const { width } = this.state;
    const isSmall = width <= 1050;

    if (isSmall) {
      return (
        <Row>
          <DropDownV2
            form="claims"
            name="claims"
            placeholderString="Death"
            dropList={['Death', 'Critical Illness', 'Disability', 'Medical', 'Personal Accident', 'Travel']}
            size="medium"
            zIndex={100}
            isDisableTyping
            notForm
            valueChangeCallback={(value) => {
              switch (value) {
                case 'Death': return (
                  this.setState({
                    isDeathActive: true,
                    isCriticalIllnessActive: false,
                    isDisabilityActive: false,
                    isMedicalActive: false,
                    isPersonalAccidentActive: false,
                    isTravelActive: false,
                  })
                );
                case 'Critical Illness': return (
                  this.setState({
                    isDeathActive: false,
                    isCriticalIllnessActive: true,
                    isDisabilityActive: false,
                    isMedicalActive: false,
                    isPersonalAccidentActive: false,
                    isTravelActive: false,
                  })
                );
                case 'Disability': return (
                  this.setState({
                    isDeathActive: false,
                    isCriticalIllnessActive: false,
                    isDisabilityActive: true,
                    isMedicalActive: false,
                    isPersonalAccidentActive: false,
                    isTravelActive: false,
                  })
                );
                case 'Medical': return (
                  this.setState({
                    isDeathActive: false,
                    isCriticalIllnessActive: false,
                    isDisabilityActive: false,
                    isMedicalActive: true,
                    isPersonalAccidentActive: false,
                    isTravelActive: false,
                  })
                );
                case 'Personal Accident': return (
                  this.setState({
                    isDeathActive: false,
                    isCriticalIllnessActive: false,
                    isDisabilityActive: false,
                    isMedicalActive: false,
                    isPersonalAccidentActive: true,
                    isTravelActive: false,
                  })
                );
                case 'Travel': return (
                  this.setState({
                    isDeathActive: false,
                    isCriticalIllnessActive: false,
                    isDisabilityActive: false,
                    isMedicalActive: false,
                    isPersonalAccidentActive: false,
                    isTravelActive: true,
                  })
                );
                default: return null;
              }
            }
          }
          />
          {this.renderDocument(12)}
        </Row>
      );
    }

    return (
      <Row>
        <Col xs="5" className="category-section">
          {
            _.map(data.categories, c => (
              this.renderCategory(c)
            ))
          }
        </Col>
        {this.renderDocument(7)}
      </Row>
    );
  }

  render() {
    return (
      <Col
        xs={{ size: 10, offset: 1 }}
        className="fillings-guide"
      >
        <Row>
          <p className="title">Fillings Guide</p>
          <p className="sub-title">Select the relevant guide below to read the claims submission process</p>
        </Row>
        {this.renderGuide()}
      </Col>
    );
  }
}

Category.defaultProps = {
};

Category.propTypes = {
  data: PropTypes.object.isRequired,
};

export default Category;

